import { useCallback, useEffect, useState } from "react";
import { validation } from './validation'
import Axios from "../../api/Axios";
import { Link, useLocation, useNavigate, useSearchParams } from 'react-router-dom';
import "./index.css"
import { isEqual } from "lodash";
import { useSelector } from "react-redux";
import Loader from "../../components/Loader";
import Swal from 'sweetalert2'
import withReactContent from 'sweetalert2-react-content'
import { cartActions } from "../../store/cart";
import { useDispatch } from "react-redux";
import store from "../../store";
import { Swiper, SwiperSlide } from 'swiper/react';
import triggerSwalAlert from "../../helpers/triggerSwalAlert";
import trackUserEvent from "../../helpers/trackUserEvent";
import ThankYouPage from "../ThankYou";
const MySwal = withReactContent(Swal)

const axios = new Axios();

const CheckOut = () => {

    const navigate = useNavigate()
    const location = useLocation()
    const [pending, setPending] = useState(false)
    const [error, setError] = useState({
        firstName: false,
        lastName: false,
        email: false,
        phone: false,
    });
    const currentUser = store.getState().user.currentUser
    let [billingAddress, setBillingAddress] = useState([])
    let [shippingAddress, setShippingAddress] = useState([])
    const [freeProducts, setFreeProducts] = useState([]);
    let [popups,setPopups] = useState([]);
    const [selectedBillingAddress, setSelectedBillingAddress] = useState(null)
    const [selectedShippingAddress, setSelectedShippingAddress] = useState(null)
    const [showBillingAddress, setShowBillingAddress] = useState(false)
    const [cartProducts, setCartProducts] = useState([]);
    const [selectedCartProducts, setSelectedCartProducts] = useState([]);
    const selectedList = useSelector((state) => state.cart.itemsList);
    const [noAddressAvailable, setNoAddressAvailable] = useState(false)
    const [subtotalWithoutDiscount, setSubTotalWithoutDiscount] = useState(0);
    const [subtotalWithDiscount, setSubTotalWithDiscount] = useState(0);
    const [totalDiscount, setTotalDiscount] = useState(0);
    const [shippingCharges, setShippingCharges] = useState(0);
    const [orderType, setOrderType] = useState("COD")
    const [searchParams, setSearchParams] = useSearchParams();
    const couponCode = searchParams.get("couponCode")
    const [couponAmount, setCouponAmount] = useState(0);
    const dispatch = useDispatch();
    const [data, setData] = useState({
        firstName: currentUser.firstName,
        lastName: currentUser.lastName,
        email: currentUser.email,
        phone: currentUser.contact,
        notes: ""
    });
    const [orderPlaced, setOrderPlaced] = useState(false)
    const [orderId, setOrderId] = useState(null)
    const [orderTotalAmount, setOrderTotalAmount] = useState(0)


    const onChange = (value, target) => {
        setData({
            ...data,
            [target]: value,
        });
        if (error.email === true || error.firstName === true || error.lastName === true || error.phone) {
            setError({
                ...error,
                [target]: false
            })
        }
    };

    const loadScript = (src) => {
        return new Promise((resolve) => {
            const script = document.createElement("script");
            script.src = src;
            script.onload = () => {
                resolve(true);
            };
            script.onerror = () => {
                resolve(false);
            };
            document.body.appendChild(script);
        });
    }

    const handleRazorpayPayment = async (order, payload)=>{

        let getUserDetailsResponse = await axios.callApi(`user/getUserDetails`, {method: "GET"})
        let currentUser = getUserDetailsResponse.data.user

        let options = {
            "key": process.env.REACT_APP_RAZORPAY_KEY_ID, // Enter the Key ID generated from the Dashboard
            "amount": order.amount, // Amount is in currency subunits. Default currency is INR. Hence, 50000 refers to 50000 paise
            "currency": "INR",
            "name": `Order Payment`,
            "description": "",
            "image": "",
            "order_id": order.id, //This is a sample Order ID. Pass the `id` obtained in the response of Step 1
            "handler": async function (response){
                setPending(true)
                let axiosOption = {
                    method: "POST",
                    body: {
                        payload,
                        razorpay_payment_id: response.razorpay_payment_id,
                        razorpay_order_id: response.razorpay_order_id,
                        razorpay_signature: response.razorpay_signature
                    }
                }
                let result = await axios.callApi("/order/paymentVerificationAndCreateOrder", axiosOption)
                if(result.status == 200){
                    setPending(false)
                    dispatch(cartActions.addToCart([]))
                    // navigate("/")
                    // return MySwal.fire({
                    //     title: <strong>ORDER PLACED</strong>,
                    //     html: <p>Your order has been placed successfully</p>,
                    //     icon: 'success'
                    // })
                    setOrderId(result?.data?.data?.orderId)
                    setOrderTotalAmount(result?.data?.data?.orderTotalAmount)
                    setOrderPlaced(true)
                    window.history.replaceState(null, '', '/thank-you');
                }
                else{
                    setPending(false)
                    return MySwal.fire({
                        title: <strong>Error</strong>,
                        html: <p>Something went wrong while making your order. Please try again in sometime</p>,
                        icon: 'error'
                    })
                }

            },
            "prefill": {
                "name": `${currentUser.firstName} ${currentUser.lastName}`,
                "email": `${currentUser.email}`,
                "contact": ""
            },
            "notes": {
                "address": "Razorpay Corporate Office"
            },
            "theme": {
                "color": "#121212"
            }
        };
        let razor = new window.Razorpay(options);
        razor.open()
        razor.on('payment.failed', function (response) {
            // alert(response.error.code);
            // alert(response.error.description);
            // alert(response.error.source);
            // alert(response.error.step);
            // alert(response.error.reason);
            // alert(response.error.metadata.order_id);
            // alert(response.error.metadata.payment_id);
        });
    }

    const handleSubmit = async (e) => {
        try {
            e.preventDefault()
            const valid = validation(data)
            setError(valid)
            if (valid.firstName || valid.lastName || valid.email || valid.phone) {
                return
            }
            if (!selectedBillingAddress || !selectedShippingAddress) {
                return MySwal.fire({
                    title: <strong>Error</strong>,
                    html: <p>You need to select billing and shipping addresses</p>,
                    icon: 'error'
                })
            }
            setPending(true)
            if(orderType == "RAZORPAY"){
                const razorpayScript = await loadScript(
                    "https://checkout.razorpay.com/v1/checkout.js"
                );
            }
            let addressDetails = {
                shippingAddress: { ...selectedShippingAddress, firstName: data.firstName, lastName: data.lastName, email: data.email, contact: data.phone },
                billingAddress: { ...selectedBillingAddress, firstName: data.firstName, lastName: data.lastName, email: data.email, contact: data.phone }
            }
            let payload = {
                addressDetails,
                selectedList,
                couponCode: couponCode || null,
                orderNotes: data.notes,
                paymentMethod: orderType
            }
            const options = {
                method: "POST",
                body: payload
            }

            const result = await axios.callApi(`order/createOrder`, options)
            if (result.status == 200) {
                setPending(false)
                if(orderType == "RAZORPAY"){
                    return await handleRazorpayPayment(result?.data?.data, payload)
                }
                dispatch(cartActions.addToCart([]))
                // navigate("/")
                // return MySwal.fire({
                //     title: <strong>ORDER PLACED</strong>,
                //     html: <p>Your order has been placed successfully</p>,
                //     icon: 'success'
                // })
                setOrderId(result?.data?.data?.orderId)
                setOrderTotalAmount(result?.data?.data?.orderTotalAmount)
                setOrderPlaced(true)
                window.history.replaceState(null, '', '/thank-you');
            }
            else {
                setPending(false)
                return MySwal.fire({
                    title: <strong>Error</strong>,
                    html: <p>Something went wrong while making your order. Please try again in sometime</p>,
                    icon: 'error'
                })
            }
        }
        catch (error) {
            console.error("Something went wrong")
        }
    }

    const handleGetFreeProducts = useCallback(async () => {
        let unlockedPopup
        if(subtotalWithDiscount != 0 && popups.length){
          let tempPopups = [...popups]
          tempPopups.reverse()
          tempPopups.every((popup) => {
            if(subtotalWithDiscount >= popup.minimumAmount){
              unlockedPopup = popup
              return false
            }
            else{
              return true
            }
          })
        }
        if(!unlockedPopup) {
            setFreeProducts([])
            return
        }
    
        setFreeProducts(unlockedPopup.products)
    }, [popups, subtotalWithDiscount])

    const getAllPopups = useCallback(async () => {
        setPending(true)
        let options = {
          method: "GET"
        }
        let result = await axios.callApi("/popup/getAllPopups", options);
        if (result?.status == 200) {
          setPending(false)
          popups = result?.data?.data
          setPopups(result?.data?.data)
        }
        else {
          setPending(false)
        }
    }, [])

    const getUserAddressDetails = async () => {
        const options = {
            method: "GET"
        }
        const result = await axios.callApi(`user/getAddresses`, options)
        if (result.status == 200) {
            if(result?.data?.data.length == 0){
                setNoAddressAvailable(true)
            }
            billingAddress = result?.data?.data?.filter(address => address.addressType == "billing")
            setBillingAddress([...billingAddress])
            shippingAddress = result?.data?.data?.filter(address => address.addressType == "shipping")
            setShippingAddress([...shippingAddress])
            setSelectedBillingAddress(shippingAddress[0])
            setSelectedShippingAddress(shippingAddress[0])
        }
    }

    useEffect(() => {
        getUserAddressDetails()
    }, [])

    const handleAddNewClick = (data) => {
        navigate(`/addEditAddress/${data}-address${noAddressAvailable ? "?firstAddress=true" : ""}`)
    }

    const handleShippingAddressChange = (address) => {
        setSelectedShippingAddress(address)
        if (!showBillingAddress) {
            setSelectedBillingAddress(address)
        }
    }

    const getProductsInCart = useCallback(async () => {
        try {
            setPending(true);
            const options = {
                method: "POST",
                body: selectedList,
            };
            const result = await axios.callApi(`/product/getProductsInCart`, options);
            if (result?.status == 200) {
                setPending(false);
                let cartProducts = result?.data?.data
                let selectedCartProducts = result?.data?.data?.map(item => {
                    return item?.productDetails?.find(x => isEqual(x.attributeCombination, item.attributeCombination))
                })
                setCartProducts(cartProducts);
                setSelectedCartProducts(selectedCartProducts)
                setAmountSummary(cartProducts, selectedCartProducts);
            } else {
                setPending(false);
            }
        } catch (err) {
            setPending(false);
        }
    }, [selectedList]);

    useEffect(() => {
        getProductsInCart();
    }, []);

    useEffect(() => {
        getAllPopups();
    }, []);

    useEffect(() => {
        trackUserEvent("Checkout")
    },[])

    useEffect(() => {
        handleGetFreeProducts()
    },[popups, subtotalWithDiscount])

    const setAmountSummary = (cartProducts, selectedCartProducts, isCouponAppliedToCompleteCart=false, couponValue=null) => {
        let totalAmountWithoutDiscount = 0;
        let totalAmountWithDiscount = 0;
        let totalDiscount = 0;
        let deliveryCharges = 0;
        cartProducts.forEach((product, index) => {
            let selectedProduct = selectedCartProducts[index]
            totalAmountWithoutDiscount +=
                selectedProduct.productPrice * product.count;
            totalAmountWithDiscount +=
                selectedProduct.productDiscountPrice * product.count;
            let discount =
                selectedProduct.productPrice * product.count -
                selectedProduct.productDiscountPrice * product.count;
            totalDiscount += discount;
        });
        if (isCouponAppliedToCompleteCart) {
            let couponAmountTemp = Math.round(totalAmountWithDiscount * couponValue / 100)
            setCouponAmount(couponAmountTemp)
            totalAmountWithDiscount = totalAmountWithDiscount - couponAmountTemp
        }
        if (totalAmountWithDiscount < 500) {
            deliveryCharges = 50
        }
        totalAmountWithDiscount = totalAmountWithDiscount + deliveryCharges
        handleGetFreeProducts(totalAmountWithDiscount)
        setSubTotalWithDiscount(totalAmountWithDiscount);
        setSubTotalWithoutDiscount(totalAmountWithoutDiscount);
        setTotalDiscount(totalDiscount);
        setShippingCharges(deliveryCharges)
    };

    useEffect(() => {
        if(!location?.state?.showPopup){
           return
        }
        {
          subtotalWithDiscount != 0 && popups.length && subtotalWithDiscount < popups[popups.length -1].minimumAmount && MySwal.fire({
            title: <strong>Available offer</strong>,
            html: <Swiper
              style={{ fontSize: "2.5rem", cursor: "default" }}
              slidesPerView={1}
            >
              {popups.map((popup) => {
                return subtotalWithDiscount < popup?.minimumAmount && <SwiperSlide>
                  <img src={process.env.REACT_APP_BASE_URL + "/" + popup?.image?.url} alt="" style={{ width: '17rem', height: "12rem" }} /> <br />
                </SwiperSlide>
              })}

            </Swiper>
          })
        }
      }, [popups,subtotalWithDiscount])

    function findProductWithVariantId(product) {
        return (
            product.productId == this.productId &&
            product.productVariantId == this.productVariantId
        );
    }
    
    useEffect(() => {
        if(!couponCode || !cartProducts.length) return

        let options = {
            method: "POST",
            body: {
                selectedList,
                couponCode
            }
        }
        axios.callApi("/coupon/validateAndApplyCoupon", options).then(result => {
            if (result?.status == 200) {
                let cartProductTemp = JSON.parse(JSON.stringify(cartProducts));
                let selectedCartProductsTemp = JSON.parse(JSON.stringify(selectedCartProducts))
                if (result?.data?.data?.products && result?.data?.data?.products.length) {
                    for (let i = 0; i < result?.data?.data?.products.length; i++) {
                        let product = result?.data?.data?.products[i]
                        let couponProductIndex = cartProductTemp.findIndex(findProductWithVariantId, product)
                        if (couponProductIndex != -1) {
                            selectedCartProductsTemp[couponProductIndex].productDiscountPrice = Math.round(selectedCartProductsTemp[couponProductIndex].productDiscountPrice - selectedCartProductsTemp[couponProductIndex].productDiscountPrice * result.data.data.couponValue / 100)
                        }
                    }
                    setSelectedCartProducts([...selectedCartProductsTemp])
                }
                setAmountSummary(cartProductTemp, selectedCartProductsTemp, result?.data?.data?.products == null, result?.data?.data?.couponValue);
            }
            else {
                triggerSwalAlert("Error", result?.data?.errorMessage || "Something went wrong", "error")
            }
        });
    }, [cartProducts])

    if(orderPlaced && orderId && orderTotalAmount){
        return <ThankYouPage orderPlaced={orderPlaced} orderId={orderId} orderTotalAmount={orderTotalAmount}/>
    }

    return (
        <>
            {pending ? <Loader /> : null}
            <div className="checkout-container container pb-4" style={{ backgroundColor: "white" }}>
                <span className="heading">CHECKOUT DETAILS</span>
                <form>
                    <div className="row">
                        <div className="col-1"></div>
                        <div className="col-lg-10 col-sm-12">
                            <div className="text-style">
                                BILLING DETAILS
                            </div>
                            <div className="row" style={{ marginTop: "25px" }}>
                                <div className='col-lg-6 col-sm-12 form-group mb-2'>
                                    <label htmlFor="firstName" className='form-label'>First Name</label>
                                    <input type="text" id='firstName' className={`form-control ${error.firstName ? "errorInputField" : ""}`} required onChange={e => onChange(e.target.value, 'firstName')} value={data.firstName} />
                                    {error.firstName && <div className='error'>
                                        Please enter your first name
                                    </div>}
                                </div>
                                <div className='col-lg-6 form-group mb-2'>
                                    <label htmlFor="lastName" className='form-label'>Last Name</label>
                                    <input type="text" id='lastName' className={`form-control ${error.lastName ? "errorInputField" : ""}`} required onChange={e => onChange(e.target.value, 'lastName')} value={data.lastName} />
                                    {error.lastName && <div className='error'>
                                        Please enter your last name
                                    </div>}
                                </div>
                            </div>
                            <div className="row">
                                <div className="col-lg-6 mt-5">
                                    <div className='form-group mb-2'>
                                        <label htmlFor="email" className="form-label">Email</label>
                                        <input type="email" id='email' className={`form-control ${error.email ? "errorInputField" : ""}`} onChange={e => onChange(e.target.value, 'email')} value={data.email} />
                                        {error.email && <div className='error'>
                                            {error.message ? "Please enter a valid email" : "Please enter your email"}
                                        </div>}
                                    </div>
                                    <div className='form-group mb-2'>
                                        <label htmlFor="phone" className="form-label">Phone No.</label>
                                        <input type="number" id='phone' className={`form-control ${error.phone ? "errorInputField" : ""}`} onChange={e => onChange(e.target.value, 'phone')} value={data.phone} />
                                        {error.phone && <div className='error'>
                                            Please enter valid phone number
                                        </div>}
                                    </div>
                                </div>
                                <div className="col-lg-6">
                                    <div className="text-style" style={{ marginBottom: "1rem" }}>
                                        ADDITIONAL INFORMATION
                                    </div>
                                    <div style={{ display: "flex", flexDirection: "column" }}>
                                        <strong style={{ marginBottom: "0.3rem" }}>
                                            Order notes (optional)
                                        </strong>
                                        <textarea cols="10" rows="4" placeholder="Notes about your order, e.g. special notes for delivery." onChange={e => onChange(e.target.value, 'notes')}></textarea>
                                    </div>
                                </div>
                                <div className="col-lg-6 mt-4">
                                    <div className="title ps-0 mb-3">Select a Shipping (Delivery) address</div>
                                    <div className="form-group">
                                        {shippingAddress?.map((item) => {
                                            return (
                                                <label className="card p-2 mb-3" htmlFor={`shippingAddressRadio${shippingAddress.indexOf(item)}`}>
                                                    <span>
                                                        <div className="d-flex align-items-start">
                                                            <input type="radio" className="me-2 mt-2" name="shippingAddressRadio" id={`shippingAddressRadio${shippingAddress.indexOf(item)}`} defaultChecked={selectedShippingAddress ? isEqual(selectedShippingAddress, item) ? true : false : shippingAddress.indexOf(item) == 0 ? true : false} onChange={() => handleShippingAddressChange(item)} />
                                                            {item ? <div className="text-bold-md">
                                                                {item?.companyName && <><i>{`${item?.companyName}`}</i><br /></>}
                                                                <i>{`${item?.streetAddress}`}</i><br />
                                                                {item?.landmark && <><i>{`${item?.landmark}`}</i><br /></>}
                                                                <i>{`${item?.city} ${item?.pincode}`}</i><br />
                                                                <i>{`${item?.state}`}</i>
                                                                <br />
                                                                <br />
                                                                <Link to={`/addEditAddress/shipping-address?id=${item.id}`} className="mt-4" style={{ textDecoration: "underline", color: "#777777" }}>{"Edit"}</Link>
                                                                <br />
                                                            </div> : ''}
                                                        </div>
                                                    </span>
                                                </label>
                                            )
                                        })}
                                    </div>
                                    <button className="btn btn-dark" data-bs-toggle="modal" data-bs-target="#addAddress" onClick={() => handleAddNewClick('shipping')}>Add New Shipping Address</button>
                                    {/* billing Details  */}
                                    <br />
                                    <label htmlFor="" className="mt-4">
                                        <input type="radio" name="billingAddressRadio"
                                            onChange={() => {
                                                setShowBillingAddress(false)
                                                setSelectedBillingAddress(selectedShippingAddress)
                                            }} defaultChecked /><span className="title">Billing address same as Shipping address</span>
                                    </label>
                                    <br />
                                    <label htmlFor="" className="mt-4">
                                        <input type="radio" name="billingAddressRadio"
                                            onChange={() => {
                                                setShowBillingAddress(true)
                                                setSelectedBillingAddress(billingAddress[0])
                                            }} /><span className="title">Select Another Billing address</span>
                                    </label>
                                    {showBillingAddress ?
                                        <>
                                            <div className="form-group">
                                                {billingAddress?.map((item) => {
                                                    return (
                                                        <label className="card p-2 mb-3" htmlFor={`billingAddressRadio${billingAddress.indexOf(item)}`}>
                                                            <span>
                                                                <div className="d-flex align-items-start">
                                                                    <input type="radio" className="me-2 mt-2" name="billingAddressRadio" id={`billingAddressRadio${billingAddress.indexOf(item)}`} defaultChecked={billingAddress.indexOf(item) == 0 ? true : false} onChange={() => setSelectedBillingAddress(item)} />
                                                                    {item ? <div className="text-bold-md">
                                                                        {item?.companyName && <><i>{`${item?.companyName}`}</i><br /></>}
                                                                        <i>{`${item?.streetAddress}`}</i><br />
                                                                        {item?.landmark && <><i>{`${item?.landmark}`}</i><br /></>}
                                                                        <i>{`${item?.city} ${item?.pincode}`}</i><br />
                                                                        <i>{`${item?.state}`}</i>
                                                                        <br />
                                                                        <br />
                                                                        <Link to={`/addEditAddress/billing-address?id=${item.id}`} className="mt-4" style={{ textDecoration: "underline", color: "#777777" }}>{"Edit"}</Link>
                                                                        <br />
                                                                    </div> : ''}
                                                                </div>
                                                            </span>
                                                        </label>
                                                    )
                                                })}
                                            </div>
                                            <button className="btn btn-dark" data-bs-toggle="modal" data-bs-target="#addAddress" onClick={() => handleAddNewClick('billing')}>Add New Billing Address</button>
                                        </>
                                        : <></>
                                    }

                                </div>
                                <div className="col-lg-6 mt-4">
                                    <div className="text-style" style={{ fontSize: "1.2rem" }}>
                                        YOUR ORDER
                                    </div>
                                    <div className="order-section ps-0">
                                        <div className="heading">
                                            <span>PRODUCT</span>
                                            <span>SUB TOTAL</span>
                                        </div>
                                        {cartProducts.map((product, index) => {
                                            return (
                                                <div className="orders">
                                                    <span>{product?.productName + " " + Object.values(product.attributeCombination)}  × {product.count}</span>
                                                    <span>₹{selectedCartProducts[index]?.productDiscountPrice * product?.count}</span>
                                                </div>
                                            )
                                        })}
                                        {freeProducts.map((product) => {
                                            return (
                                                <div className="orders">
                                                    <span>{product?.productName + " " + Object.values(product?.attributeCombination)}  × {product?.count}</span>
                                                    <span>FREE</span>
                                                </div>
                                            )
                                        })}
                                        {couponAmount ? (
                                            <div className="heading">
                                                <span>COUPON APPLIED</span>
                                                <div className="total-price">
                                                    <span style={{ textAlign: "end" }}>₹{couponAmount}</span>
                                                </div>
                                            </div>
                                        ) : null}
                                        <div className="heading">
                                            <span>DELIVERY</span>
                                            <div className="total-price">
                                                <span style={{ textAlign: "end" }}>₹{shippingCharges}</span>
                                                <span className="tax-info">{shippingCharges == 0 ? `(Free Delivery over ₹500)` : ""}</span>
                                            </div>
                                        </div>
                                        <div className="heading">
                                            <span>Total</span>
                                            <div className="total-price">
                                                <span style={{ textAlign: "end" }}>₹{subtotalWithDiscount}</span>
                                                <span className="tax-info">{selectedShippingAddress?.state?.toLowerCase() == "maharashtra" ? `(includes ₹${(subtotalWithDiscount * 9 / 100).toFixed(1)} 9% CGST, ₹${(subtotalWithDiscount * 9 / 100).toFixed(1)} 9% SGST)` : `(includes ₹${(subtotalWithDiscount * 18 / 100).toFixed(1)} 18% GST)`}</span>
                                            </div>
                                        </div>
                                        <div className="mt-3">Orders usually dispatch in 1-2 working days</div>
                                        <label htmlFor="" className="mt-4">
                                            <input type="radio" name="orderType"
                                                onChange={() => {
                                                    setOrderType("COD")
                                                }} defaultChecked /><span className="title">Cash on delivery</span>
                                        </label>
                                        <br />
                                        <label htmlFor="" className="mt-4">
                                            <input type="radio" name="orderType"
                                                onChange={() => {
                                                    setOrderType("RAZORPAY")
                                                }} /><span className="title">Credit Card/Debit Card/NetBanking <img src="https://cdn.razorpay.com/static/assets/logo/payment.svg" alt="Credit Card/Debit Card/NetBanking"></img></span>
                                        </label>
                                        <button className='btn btn-dark mt-3 mb-3' onClick={(e) => handleSubmit(e)}>PLACE ORDER</button>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div className="col-1"></div>
                    </div>
                </form>
            </div>
        </>
    )
}

export default CheckOut
