import React from 'react';
import successGreenCheckMarkIcon from '../../assets/success-green-check-mark-icon.svg';
import { useNavigate } from 'react-router-dom';
import './index.css';

const ThankYouPage = ({orderPlaced, orderId, orderTotalAmount}) => {
  const navigate = useNavigate();
  if(!orderPlaced){
    navigate("/");
    return null;
  }
  return (
    <div className="container thankYouPageContainer">
      <div className="row">
        <div className="col-12 col-md-8 offset-md-2 text-center">
          <img className="mt-5 successGreenCheckMarkImg" src={successGreenCheckMarkIcon} alt="Success Icon" />
          <h1 className="display-3 mt-3"><strong>Thank you.</strong></h1>
          <p className="lead mt-3">
            <strong><span className="orderPlacedText">Your order has been placed successfully.</span></strong>
          </p>
          <p className="lead emailInvoiceText">
            <i className="fas fa-envelope"></i>
            <strong><span>An email invoice including the details about your order has been sent to the email address provided.</span></strong>
          </p>
          <p className="lead mt-4 mb-4 continueShopping">
                <button onClick={()=>navigate("/")}>
                  Continue Shopping
                </button>
          </p>
        </div>
      </div>
    </div>
  );
};

export default ThankYouPage;